import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { HttpClient, HttpHeaders }  from '@angular/common/http';
import { HostProvider } from '../../providers/host/host';

@IonicPage()
@Component({
  selector: 'page-filter',
  templateUrl: 'filter.html',
})
export class FilterPage {
  buttonCheckBox: any;
  item: any;
  dataFilter: any;
  keys: any;
  selectedArray :any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public host: HostProvider, public http: HttpClient) {
    this.getBahanBaku();
    this.initData();
  }

  public closeModal(){
    this.viewCtrl.dismiss();
  }

  // DATA Filter
  getBahanBaku(){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Accept':  'application/*',
      })
    };

  this.http.get(this.host.baseUrl + "/v1/data/kategori-produk", httpOptions)
  .subscribe(
    data => {
      this.dataFilter = [];
      this.keys = Object.keys(data);

      for(let i = 0; i < this.keys.length; i++){
        this.dataFilter.push({
          'key': this.keys[i],
          'data': data[this.keys[i]]
        });
      }

      this.item = this.dataFilter;
      console.log(this.dataFilter);
    }, (err) => {
      console.log("error : " + err);
      console.log(err);
    });
  }  

  initData(){
    this.dataFilter = this.item;
  }

  getFilterProduk(item: any, $event){
    return this.item.map((itemProduk) => {
      return itemProduk.data["0"].nama_produk;
    });
  }

  CheckboxProdukClicked(item: any, $event) {
    if (item == 'checked') {
      console.log('Checked Produk :' + item.data["0"].nama_produk);
    } else {
      console.log('Unchecked');
    }
  }

  CheckboxDaerahClicked(item: any, $event) {
    console.log('Checked Daerah : ' + item.data["0"].nama_daerah);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FilterPage');
  }

}



